.testimonials {
  &__item {
    margin-bottom: 48px;

    &:last-child {
      margin-bottom: 0;
    }

    @include onDesktop {
      margin-bottom: 0;
    }
  }
}

.testimonial {
  &__photo-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 24px;

    margin-bottom: 24px;

    @include onDesktop {
      margin-bottom: 32px;
    }
  }

  &__photo {
    grid-column: 2 / 4;
    border-radius: 50%;

    @include testimonialSize-mobile;

    @include onDesktop {
      @include testimonialSize-desktop;
    }
  }

  &__name {
    text-decoration: none;
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    text-align: center;
    color: $colorBlue;

    margin: 0 0 4px;
  }

  &__role {
    font-weight: normal;
    font-size: 14px;
    line-height: 27px;
    text-align: center;
    margin: 0 0 4px;

    color: #9fadc2;
  }

  &__about {
    font-size: 16px;
    line-height: 27px;
    text-align: center;
    color: #3e3e3e;

    margin: 0;
  }
}
